@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;600;700;800;900&display=swap");

/*
PC ( 1024px)
@media all and (min-width:1024px) {
  ...
}

 ,   ( 768px ~ 1023px)
@media all and (min-width:768px) and (max-width:1023px) {
  ...
}

 ,   ( 480px ~ 767px)
@media all and (max-width:767px) {
  ...
}
*/

body {
  background-color: white;
  margin: 0;
  font-family: "Noto Sans KR ";
}

/*
-----------------
-----Navbar------
-----------------
*/

/* PC */
@media all and (min-width: 1024px) {
  .navbar {
    width: 100%;
    height: 72px;
    background-color: rgba(255, 255, 255, 1);
    font-size: 16px;
    font-family: "Noto Sans KR";
    font-weight: 700;
    color: rgba(60, 52, 51, 1);
  }

  .navbar-container {
    max-width: 1200px;
    height: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .navbar-logo-container {
    width: 150px;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  }

  .navbar-logo {
    text-decoration: none;
    cursor: pointer;
  }

  .navbar-logo-img {
    width: 35px;
    height: 48px;
  }

  .navbar-menu-container {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  .navbar-menu {
    padding-left: 20px;
    padding-right: 20px;
    text-decoration: none;
    cursor: pointer;
    color: inherit;
  }
}

